class Launcher1 {

    run(): void {
        const root: any = window;
        root["response"] = "This response is passed to window";
        root.context["response"] = "HELLO WORLD!";

        console.log("Launcher1.run()");
        console.log("window:", JSON.stringify(root));
        console.log("window.params():", JSON.stringify(root.params()));
    }
}

// ---------------------------------------------------------------
//  l a u n c h e r
// ---------------------------------------------------------------

try {
    const launcher = new Launcher1();
    launcher.run();
} catch (err) {
    console.error("Launcher1: ", err);
}

